.card {
  background-color: #1a1b21;
  color: #ffffff;
  border-radius: 10px;
  overflow: hidden;
  width: 317px;
}

.card--header {
  width: 317px;
  height: 317px;
  background-image: url(./img/profile-pic.jpg);
  background-size: cover;
  background-position: 50% 50%;
}

/* MAIN */

.main {
  text-align: center;
  padding: 0 36px;
  background-color: #1a1b21;
}

.main--name {
  color: #fff;
  font-size: 1.5625rem;
  font-weight: 400;
  margin-top: 21px;
}

.main--profession {
  color: #f3bf99;
  font-weight: 700;
  font-size: 0.8rem;
  margin-top: 6.19px;
}

.main--website {
  color: #f5f5f5;
  font-weight: 700;
  font-size: 0.64rem;
  margin-top: 8.81px;
}

.main--buttons {
  display: flex;
  justify-content: space-around;
  margin-top: 15px;
  /* padding: 0 2rem; */
}

/* BUTTONS */

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
  border: none;
  font-family: inherit;
  font-weight: 700;
  padding: 9px 13px;
  width: 115px;
  height: 34px;
  background: #ffffff;
  box-shadow: 0px 1px 2px rgba(0, 0, 0, 0.05);
  border-radius: 6px;
  cursor: pointer;
}

.icon {
  margin-right: 8px;
}

.btn-secondary {
  background: #5093e2;
  color: white;
}

/* ARTICLES */

.section {
  padding: 24px 0px;
}

.article {
  text-align: left;
}

.article:first-child {
  margin-bottom: 18px;
}

.article h2 {
  font-size: 16px;
  font-weight: 400;
}

.article p {
  margin-top: 5px;
  font-size: 0.64rem;
  font-weight: 700;
  line-height: 0.96rem;
}

.footer {
  background: #161619;
  height: 64px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.footer--socials {
  list-style-type: none;
  display: flex;
  justify-content: center;
  align-items: center;
}

.social{
  font-size: 25px;
  color: #918E9B;
  cursor: pointer;
}

.footer--socials li:not(:last-child){
  margin-right: 24px;
}
