/* DEFAULT */
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: 'Inter', sans-serif;
  font-size: 16px;
}

body {
  width: 100%;
  min-height: 100vh;
  background-color: #23252C;
  display: flex;
  justify-content: center;
  align-items: center;
}


